:root {
  --green: #12e177;
  --orange: #ffc72a;
  --blue: #43d443;
}

body {
  font-family: "Roboto", sans-serif;
  padding: 0;
  margin: 0;
}

button {
  background: blue;
  padding: 8px;
  border-radius: 8px;
  border: 1px solid black;
  outline: none;
}

button:hover {
  cursor: pointer;
}

.app {
  font-size: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.text-green {
  color: var(--green);
}

.text-orange {
  color: var(--orange);
}

.history-wrapper {
  width: 300px;
  text-align: center;
  margin-bottom: 20px;
}

.history-wrapper .history {
  display: inline-block;
  padding: 0;
  margin: 0;
}

.history-wrapper .history li {
  list-style: none;
  text-align: left;
}

.history-wrapper .history li:before {
  content: "";
  border-radius: 50%;
  display: inline-block;
  height: 5px;
  width: 5px;
  background-color: var(--green);
  margin-right: 4px;
  margin-bottom: 1px;
}

.history-wrapper .history li .btn-move {
  color: #fff;
}

.history-wrapper .history li .btn-move.active {
  font-weight: bold;
  color: var(--green);
}

.status-message {
  margin-bottom: 30px;
  font-size: 1.2rem;
  font-weight: lighter;
}

.status-message span {
  font-weight: normal;
}

.btn-reset {
  font-size: 0.8rem;
  color: #fff;
  border-radius: 15px;
  padding: 12px 18px;
  margin-top: 25px;
  transition: all 0.2s;
  background-color: var(--blue);
  box-shadow: 0px 0px 0px 1px var(--orange);
}

.btn-reset.active {
  background-color: var(--orange);
  box-shadow: none;
}

.board {
}

.board .board-row {
  display: flex;
  flex-direction: row;
  border-bottom: 2px solid #000;
}

.board .board-row:last-child {
  border-bottom: none;
}

.board .square {
  width: 80px;
  height: 80px;
  border-right: 2px solid #000;
  font-size: 2.5rem;
  padding: 0;
  overflow: hidden;
  transition: all 0.2s;
}

.board .square:last-child {
  border-right: none;
}

.board .square.winning {
  animation: scaleText 1.4s infinite;
}

@keyframes scaleText {
  0% {
    transform: 2.5rem;
  }
  50% {
    font-size: 3.5rem;
  }
  100% {
    font-size: 2.5rem;
  }
}

